optgroup[label] {
  color: #6e6e6e;
}

option {
  color: white;
}

.color-cell {
  height: 10px;
  width: 20px;
  min-width: 20px;
  max-width: 20px;
  border: 1.2px solid #000;
  padding: 10px;
}

.color-cell.selected {
  border: 1.2px solid white;
}

.fixed-dropdown .dropdown-menu {
  min-width: 230px;
  max-width: 230px;
  margin-left: -220px !important;
}

#colorPickerBtn {
  position: fixed;
  background-color: transparent;
  height: fit-content;
  width: fit-content;
  border: 0px;
  padding: 0px;
  margin-top: 5px;
  margin-left: 10px;
}

#colorPickerBtn svg path {
  transition: fill 0.3s ease;
}

#colorPickerBtn:hover svg path {
  fill: url(#SVGID_1_hover) !important;
}

optgroup option {
  text-indent: 100px;
}

textarea {
  padding-top: 7px !important;
  padding-left: 11px !important;
}

.hex-text {
  background-color: red !important;
}